

import Vue from 'vue'
import Vuex from 'vuex'
import App from './App'
import router from './router'
import axios from 'axios'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import XButton from 'vux/src/components/x-button/index.vue'


import VueLazyload from 'vue-lazyload'
import store from './store' 

import './assets/style/reset.less'   
import './assets/font/font.css'

import './assets/style/main.less'


Vue.config.productionTip = false

Vue.prototype.$http = axios





Vue.use(LoadingPlugin);
Vue.use(ToastPlugin);
Vue.use(XButton);


const FastClick = require('fastclick')
FastClick.attach(document.body)


Vue.use(VueLazyload, {
preLoad: 1.3,
error: 'http://img.tongxinghui.com/img-not-found.svg',
loading: 'http://img.tongxinghui.com/loading.svg?imageslim',
attempt: 1
})

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
})


router.beforeEach(function (to, from, next) {
console.log("切换之前")
store.commit('updateLoadingStatus', {isLoading: true})

if (from.query.urlType) {

if (!to.query.urlType) {

let query = to.query
query.urlType = from.query.urlType
next({
path: to.path,
query: query
})
} else {
next()
}
} else {
next()
}
next()
})

router.afterEach(function (to) {
console.log("切换之后")
store.commit('updateLoadingStatus', {isLoading: false})
})



